.product {
  display: flex;
  justify-content: space-around;
  padding-bottom: 45px;

  @media (max-width: $smallScreen) {
    flex-direction: column;
  }

  &__picture {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 50%;

    @media (max-width: $smallScreen) {
      width: 70%;
      margin: 20px auto;
      justify-content: center;
    }
  }

  &:nth-child(2n + 1) {
    flex-direction: row-reverse;

    .product__picture {
      justify-content: flex-start;

      @media (max-width: $smallScreen) {
        justify-content: center;
      }
    }

    @media (max-width: $smallScreen) {
      flex-direction: column;
    }
  }

  &__description {
    display: flex;
    flex-direction: column;
    width: 30%;

    @media (max-width: $smallScreen) {
      width: 70%;
      margin: 0 auto;
    }
  }

  &__type {
    @extend %h3;
    color: $croissantColor;

    @media (max-width: $smallScreen) {
      margin-top: 0;
    }
  }

  &__name {
    @extend %h4;
  }

  &__recipt {
    line-height: 1.5em;
  }

  &__price {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 110px;
    width: 110px;
    background-color: $creamColor;
    border-radius: 50%;

    @media (max-width: $smallScreen) {
      height: 65px;
      width: 65px;
      padding: 12px;
      font-weight: 700;
      text-align: center;
    }
  }

  &__image {
    width: 80%;
    border-radius: 10%;

    @include hoverImage;
  }

  &__slash {
    @media (max-width: $smallScreen) {
      display: none;
    }
  }
}
