.contacts {
  &__heading {
    @extend %h2;
    text-align: center;

    @media (max-width: $smallScreen) {
      background-color: $croissantColor;
      color: $milkColor;
      margin-bottom: 0;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  &__links {
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 20px;
    width: 30%;
    background-color: $croissantColor;

    @media (max-width: 1024px) {
      width: 50%;
    }

    @media (max-width: $smallScreen) {
      width: 100%;
    }
  }

  &__links-heading {
    @extend %h4;

    @media (max-width: $smallScreen) {
      text-align: center;
    }
  }

  &__office,
  &__phone,
  &__emails {
    display: flex;
    align-items: flex-start;
  }

  &__info-container {
    max-width: 1500px;
    width: 100%;
    margin: 0 auto;
    display: flex;

    @media (max-width: $smallScreen) {
      flex-direction: column;
    }
  }

  &__info {
    display: flex;
    width: 100%;
    background-color: $croissantColor;

    &--heding {
      display: block;
      font-weight: 700;
      margin-bottom: 10px;
    }

    &--text {
      margin: 0;
    }
  }

  &__phone-number {
    @extend %link;
    padding: 0;
  }

  &__email-block {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__info-icon {
    margin-right: 30px;
  }

  &__map {
    position: relative;
    z-index: 2;
    width: 70%;

    @media (max-width: 1024px) {
      width: 50%;
    }

    @media (max-width: $smallScreen) {
      width: 100%;
      height: 300px;
    }
  }

  &__google-map {
    width: 100%;
    border: none;
    height: 100%;
  }

  &__feedback {
    display: flex;
    justify-content: space-evenly;
    padding: 30px 0;
    @include container;

    @media (max-width: $smallScreen) {
      flex-direction: column-reverse;
    }
  }

  &__image-block {
    display: flex;
    align-items: center;
    width: 30%;

    @media (max-width: $smallScreen) {
      width: 90%;
      margin: 20px auto;
    }
  }

  &__feedback-img {
    width: 100%;
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;

    @media (max-width: $smallScreen) {
      width: 90%;
      margin: 0 auto;
    }
  }

  &__form-heading {
    margin-top: 0;
    font-size: 26px;
    font-weight: 400;
    line-height: 1.5em;
  }

  &__message {
    height: 200px;
    padding-top: 165px;
    margin: 20px;
  }

  &__form-button {
    @include primaryBtn;
    width: 160px;
  }
}
